            this.photoDisplay = true;
            setTimeout(() => {
              this.photoDisplay = false;
            }, 2000);
          });
          if (this.mypictures.length >= 3) { this.mypictures.pop(); }
          clearInterval(this.take_photo_timer);
          setTimeout(() => {
            this.is_take_photo = false;
            this.timer = this.temp_timer;
